import React, { useState, useEffect } from 'react';

import Navigation from './components/Navigation/Navigation';
import Home from './containers/Home/Home';
import Login from './containers/Login/Login';
import Register from './containers/Register/Register';

import { BrowserRouter as Router, Switch, Route, Redirect } from "react-router-dom";
import Upload from './containers/Upload/Upload';
import EditProfile from './containers/EditProfile/EditProfile';
import Activity from './containers/Activity/Activity';

function App() {
  const [isLogged, setisLogged] = useState(false);
  const [currentUser, setcurrentUser] = useState(null);

  useEffect(() => {
    let usr = localStorage.getItem('user');
    if (usr) {
      setisLogged(true);
      setcurrentUser(usr);
    }
  }, [])
  return (
    <div className="App">
      <Router>
        <Navigation currentUser={currentUser} isLogged={isLogged} setisLogged={setisLogged} />
        <Switch>

          <Route exact path="/">
            {isLogged ? <Home currentUser={currentUser}/> : <Redirect to="/login" />}
          </Route>

          <Route exact path="/information">
            {isLogged ? <EditProfile currentUser={currentUser}/>: <Redirect to="/" />}
          </Route>

          <Route exact path="/login">
            {isLogged ? <Redirect to="/" /> : <Login setisLogged={setisLogged} setcurrentUser={setcurrentUser} />}
          </Route>

          <Route path="/register">
            {isLogged ? <Redirect to="/" /> : <Register />}
          </Route>

          <Route path="/upload">
            {isLogged ? <Upload currentUser={currentUser}/> : <Redirect to="/login" />}

          </Route>

          <Route path="/activity">
            {isLogged ? <Activity currentUser={currentUser}/> : <Redirect to="/login" />}
          </Route>

        </Switch>

      </Router>
    </div>
  );
}

export default App;
