import React from 'react';
import download from "downloadjs";
import './fileComponent.css';

const FileComponent = ({ file, setmessage, currentUser, getFiles }) => {

    const DownloadFile = () => {
        fetch(`/download/${file.filename}`, {
            headers: {
                'Authorization': currentUser
            }
        })
            .then(res => res.blob())
            .then(blob => {
                download(blob, file.filename);
            })
            .catch(err => alert(err));
    }

    const VerifyFile = () => {
        fetch(`/verify/${file.filename}`)
            .then(res => res.json())
            .then(data => {
                if (data) {
                    setmessage(`${file.filename} verified successfully!`)
                } else {
                    setmessage(`${file.fileName} is not verified!`)
                }
            })
            .catch(err => setmessage(`Could not verify ${file.filename}`));
    }

    const EncryptFile = () => {
        fetch(`/encrypt/${file.filename}`, {
            headers: {
                'Authorization': currentUser
            }
        })
            .then(res => res.json())
            .then(data => {
                setmessage(data);
                getFiles(currentUser);
            })
            .catch(err => setmessage(err));
    }

    const DecryptFile = () => {
        fetch(`/decrypt/${file.filename}`, {
            headers: {
                'Authorization': currentUser
            }
        })
            .then(res => res.json())
            .then(data => {
                setmessage(data);
                getFiles(currentUser);
            })
            .catch(err => setmessage(err));
    }

    return (
        <>
            {(file.status === 'encrypted') ? (
                <div className='file-box'>
                    <i className="fas fa-lock" onClick={DownloadFile}></i>
                    <p>{file.filename}</p>
                    <button type="button" className="btn btn-primary pl-4 pr-4 mt-2" onClick={DecryptFile}>Decrypt</button>

                </div>
            ) : (
                    <div className='file-box'>
                        <i className="fas fa-file-alt" onClick={DownloadFile}></i>
                        <p>{file.filename}</p>
                        <a href={`/signatures/${file.filename}?username=${currentUser}`}>Check signature.</a>
                        <button type="button" className="btn btn-primary pl-4 pr-4 mt-2" onClick={VerifyFile}>Verify</button>
                        <button type="button" className="btn btn-secondary pl-4 pr-4 mt-2" onClick={EncryptFile}>Encrypt</button>
                    </div>
                )}
        </>
    );


}

export default FileComponent;
