import React, { useState, useEffect } from 'react'
import Message from '../../components/Message';
import './editProfile.css';

const EditProfile = ({ currentUser }) => {
    const [message, setmessage] = useState('');
    const [name, setname] = useState('');
    const [user, setuser] = useState({});
    const [password, setpassword] = useState('');
    const [newPassword, setnewPassword] = useState('');
    const [qr, setqr] = useState('');
    useEffect(() => {
        getUser();
    }, []);

    useEffect(() => {
        if (user.name) {
            setname(user.name);
        }
        setpassword('');
        setnewPassword('');
    }, [user])

    const getUser = () => {
        fetch(`/user`, {
            headers: {
                'Authorization': currentUser
            }
        })
            .then(res => res.json())
            .then(res => {
                setuser(res);
            })
            .catch(err => alert(err));
    }

    const updateUserName = (e) => {
        e.preventDefault();

        if (!name) {
            setmessage('Cannot set name to empty');
            return;
        }

        if (name === user.name) {
            setmessage('New name is equal to current name');
            return;

        }
        fetch(`/user/update/name`, {
            method: 'PUT',
            headers: {
                'Authorization': currentUser,
                'Content-Type': 'application/json'
            },
            body: JSON.stringify({ name })
        })
            .then(res => res.json())
            .then(res => {
                getUser();
                setmessage('Name updated successfully');
            })
            .catch(err => setmessage('Could not change name.'));
    }

    const changePassword = (e) => {
        e.preventDefault();

        if (!password || !newPassword) {
            setmessage('Password Fields cannot be empty.');
            return;
        }

        if (password === newPassword) {
            setmessage('New password cannot be equal to current password');
            return;

        }
        fetch(`/user/update/password`, {
            method: 'PUT',
            headers: {
                'Authorization': currentUser,
                'Content-Type': 'application/json'
            },
            body: JSON.stringify({ currentPassword: password, newPassword })
        })
            .then(res => {
                if (res.status !== 200) throw new Error('Password could not be changed')
                return res.json()
            })
            .then(res => {
                getUser();
                setmessage('Password updated successfully');
            })
            .catch(err => {
                console.log(err);
                setmessage('Invalid current password. Unable to change password.')
            });
    }

    const updateMultifactor = (e) => {
        fetch(`/user/update/multifactor`, {
            method: 'PUT',
            headers: {
                'Authorization': currentUser,
            },
        })
            .then(res => {
                if (res.status !== 200) throw new Error('Could not update multifactor');
                return res.json()
            })
            .then(res => {
                setmessage(res.message);
                setqr(res.qr);
            })
            .catch(err => {
                console.log(err);
                setmessage("Can't generate multifactor qr at this time")
            });
        setqr();
    }

    return (
        <div style={{ width: '90%', height: '100%', margin: '10px auto' }}>
            {message && <Message message={message} />}
            <h1 className='m-4'>User Information.</h1>
            <h2 className='ml-4'>Hi {user.username}</h2>

            <div className='mt-4' style={{ width: '100%', height: '100%', display: 'flex', margin: "0 auto" }}>
                <div className='form-container'>

                    <form id='nameDiv' onSubmit={updateUserName}>
                        <p>Name:</p>

                        <div className="form-group mx-sm-3 mb-2">
                            <input className='form-control' type='text' name="name" value={name} onChange={(e) => setname(e.target.value)} />
                        </div>
                        <button type="submit" className="btn btn-primary mb-2">Update name</button>
                    </form>

                    <form id='passwordDiv' onSubmit={changePassword} className="form-control">
                        <h3>Change Password:</h3>

                        <div className="form-group mx-sm-3 mb-2">
                            <label>Current Password</label>
                            <input className='form-control' type='password' value={password} onChange={(e) => setpassword(e.target.value)} />
                        </div>

                        <div className="form-group mx-sm-3 mb-2">
                            <label>Current Password</label>
                            <input className='form-control' type='password' value={newPassword} onChange={(e) => setnewPassword(e.target.value)} />
                        </div>

                        <div className="form-group mx-sm-3 mb-2">
                            <button type="submit" className="btn btn-primary mb-2">Change Password</button>
                        </div>

                    </form>
                </div>

                <div className='multifactor-codegen'>
                    <h3>Multifactor QR code</h3>


                    {qr? (
                        <>
                            <p>Remember to scan this code or you will lose access to your account</p>
                            <img src={qr} style={{ margin: '10px auto' }} alt="qrcode" />
                        </>) : 
                        <button onClick={updateMultifactor} className="btn btn-primary mb-2">Generate new multifactor qr code</button>
                    }
                </div>

            </div>

        </div>
    )
}

export default EditProfile;