import React from 'react';
import {
    NavLink
} from "react-router-dom";
import './navigation.css';

const Navigation = ({ setisLogged, isLogged, currentUser }) => {
    const Logout = () => {
        setisLogged(false);
        localStorage.removeItem('user');
    }
    return (
        <nav id="navbar">
            <h1 id="nav-title">TLS Website</h1>

            {
                (isLogged) ? (<ul>
                    <NavLink exact to='/' activeClassName="selected">
                        <li>
                            <p>Home</p>
                        </li>
                    </NavLink>

                    <NavLink exact to='/information' activeClassName="selected">
                        <li>
                            <p>Information</p>
                        </li>
                    </NavLink>

                    <NavLink exact to='/activity' activeClassName="selected">
                        <li>
                            <p>Activity</p>
                        </li>
                    </NavLink>

                    <NavLink to='/upload' activeClassName="selected">
                        <li>
                            <p>Upload</p>
                        </li>
                    </NavLink>
                </ul>
                ) : (<ul>
                    <NavLink to='/login' activeClassName="selected">
                        <li>
                            <p>Login</p>
                        </li>
                    </NavLink>
                    <NavLink to='/register' activeClassName="selected">
                        <li>
                            <p>Register</p>
                        </li>
                    </NavLink>
                </ul>

                    )}
            {(isLogged) && <>
                <p>Logged in as {currentUser}</p>
                <p onClick={Logout} style={{ cursor: 'pointer' }}>Logout</p>
            </>}

        </nav>
    )
};

export default Navigation;