import React, { useState, useEffect } from 'react';
import './activity.css';

const Activity = ({ currentUser }) => {
    const [logs, setlogs] = useState([]);

    useEffect(() => {
        fetch(`/logs`, {
            headers: {
                'Authorization': currentUser
            }
        })
            .then(res => res.json())
            .then(res => {
                if (res.length > 0) setlogs(res);
            })
            .catch(err => alert(err));
    }, [currentUser])
    return (
        <div style={{ width: '90%', height: '100%', margin: '10px auto' }}>
            <h1 className='m-4'>User Activity.</h1>
            { logs && (
                <table>
                    <thead>
                        <tr>
                            <th>Date</th>
                            <th>Time</th>
                            <th>Action</th>
                            <th>File (opt)</th>
                        </tr>
                    </thead>

                    <tbody>
                        {logs.map(log => {
                            const date = new Date(log.date);
                            return (<tr key={log._id}>
                                <td>{date.toLocaleDateString()}</td>
                                <td>{date.toLocaleTimeString()}</td>
                                <td>{log.action}</td>
                                <td>{log.file}</td>
                            </tr>)
                        })}

                    </tbody>

                </table>
            )}
        </div>
    )
}

export default Activity;