import React, { useState } from 'react';
import axios from 'axios';
import './login.css';
import Message from '../../components/Message';

export default function Login({ setisLogged, setcurrentUser }) {

    const [username, setusername] = useState('');
    const [password, setpassword] = useState('');
    const [message, setmessage] = useState('');
    const [twoFactorAuth, settwoFactorAuth] = useState(false);
    const [twoFactorCode, settwoFactorCode] = useState('');

    const submitForm = async e => {
        e.preventDefault();
        if (!username || !password) {
            setmessage('Missing Fields');
        }

        try {
            const res = await axios.post('/login', {
                username,
                password
            })
            settwoFactorAuth(true);
            setusername(res.data.username);
        } catch (error) {
            setmessage(error.response.data);
        }
    }

    const verifyTwoFactor = async () => {
        if (!twoFactorCode) return;

        try {
            const res = await axios.post('/login/verify', {
                username: username,
                token: twoFactorCode
            })
            localStorage.setItem('user', username);
            setmessage(res.data.message);
            setcurrentUser(username);
            settwoFactorAuth(false);
            setisLogged(true);
        } catch (error) {
            setmessage(error.response.data);
        }
    }
    return (
        <div className="container mt-5">
            {message && <Message message={message} onClick={() => setmessage('')} />}

            {(twoFactorAuth) ?
                (
                    <>
                        <div className="form-group ">
                            <input
                                type="text"
                                className="form-control"
                                value={twoFactorCode}
                                onChange={(e) => settwoFactorCode(e.target.value)}
                                placeholder="Two Factor Auth code"
                            />
                        </div>

                        <div className="form-group">
                            <button className="btn btn-primary" onClick={verifyTwoFactor}>Verify Code</button>
                        </div>
                    </>
                )
                :
                (<div className="login-container">
                    <form onSubmit={submitForm} className="login-form" method="post">
                        <h2 className="text-center mb-4">Log In</h2>
                        <div className="form-group ">
                            <input
                                type="text"
                                className="form-control"
                                value={username}
                                onChange={(e) => setusername(e.target.value)}
                                placeholder="Username"
                                required="required" />
                        </div>
                        <div className="form-group">
                            <input
                                type="password"
                                onChange={(e) => setpassword(e.target.value)}
                                value={password}
                                className="form-control"
                                placeholder="Password"
                                required="required" />
                        </div>
                        <div className="form-group">
                            <button type="submit" className="btn btn-primary btn-block">Log in</button>
                        </div>
                    </form>

                </div>)}
        </div>
    )
}
