import React, { useState, useEffect } from 'react'
import axios from 'axios';
import FileComponent from '../../components/Home/FileComponent/FileComponent';
import Message from '../../components/Message';
import './home.css';

export default function Home({ currentUser }) {
    const [files, setfiles] = useState([]);
    const [message, setmessage] = useState('')

    useEffect(() => {
        getFiles(currentUser)
    }, [currentUser]);

    const getFiles = (user) =>{
        axios.get('/files', {
            headers: {
                Authorization: user
            }
        }).then(filesRes => {
            setfiles(filesRes.data);
        })
    }
    return (
        <div className='mt-4' style={{ width: '90%', margin: "0 auto" }}>
            {message && <Message message={message} />}
            <h1 className='m-4'>Existing Files</h1>
            { (files) ?
                <div className='file-container'>
                    {files.map(el => <FileComponent key={el.filename} getFiles={getFiles} currentUser={currentUser} file={el} setmessage={setmessage} />)}
                </div> : <h2>Empty directory</h2>
            }
        </div>
    )
}
