import React, { useState } from 'react';
import Message from '../../components/Message';
import axios from 'axios';

import './register.css';

export default function Register() {

    const [name, setname] = useState('');
    const [username, setusername] = useState('');
    const [password, setpassword] = useState('');
    const [confirmPassword, setconfirmPassword] = useState('');
    const [message, setmessage] = useState('');
    const [qr, setqr] = useState('');

    const submitForm = async e => {
        e.preventDefault();
        if (!name || !username || !password || !confirmPassword) {
            setmessage('Missing Fields');
        }
        if (password !== confirmPassword) {
            setmessage('Passwords do not match');
        }

        try {
            const res = await axios.post('/register', {
                name,
                username,
                password
            })
            console.log(res.data);
            setmessage(res.data.message);
            setqr(res.data.qr);
        } catch (error) {
            setmessage(error.response.data);
        }
    }

    return (
        <div className='container mt-3'>
            {message && <Message message={message} onClick={() => setmessage('')} />}

            {qr ?
                (
                    <>
                        <h1>Scan qr in google authenticator</h1>
                        <img src={qr} style={{ margin: '0 auto' }} alt="qrcode" />
                    </>
                ) :
                (<div className="register-container mt-5">
                    <div className="login-form">
                        <form onSubmit={submitForm}>
                            <h2 className="text-center mb-4">Register</h2>
                            <div className="form-group ">
                                <input type="text" className="form-control" placeholder="Name" value={name} onChange={e => setname(e.target.value)} required="required" />
                            </div>
                            <div className="form-group ">
                                <input type="text" className="form-control" placeholder="Username" value={username} onChange={e => setusername(e.target.value)} required="required" />
                            </div>
                            <div className="form-group">
                                <input type="password" className="form-control" placeholder="Password" value={password} onChange={e => setpassword(e.target.value)} required="required" />
                            </div>
                            <div className="form-group">
                                <input type="password" className="form-control" placeholder="Confirm password" value={confirmPassword} onChange={e => setconfirmPassword(e.target.value)} required="required" />
                            </div>
                            <div className="form-group">
                                <button type="submit" className="btn btn-primary btn-block">Sign Up</button>
                            </div>
                        </form>
                    </div>
                </div>)}
        </div>
    )
}
